.burger {
  display: inline-block;
  position: relative;
  z-index: 1;
  width: 25px;
  padding: 0;
  line-height: 25px;
  background: transparent;
  border: none;
  user-select: none;
  cursor: pointer; }
  @media (min-width: 768px) {
    .burger {
      display: none; } }
  .burger > span {
    /*это центральная полоса бургера*/
    display: inline-block;
    position: relative;
    width: 100%;
    height: 3px;
    margin-top: -4px;
    vertical-align: middle;
    font-size: 0;
    color: transparent;
    background: #000000;
    transition: background 0.2s 0.2s; }
    .burger > span::before, .burger > span::after {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background: #000000;
      transform-origin: 50% 50%;
      transition: top 0.2s 0.2s, transform 0.2s; }
    .burger > span::before {
      top: -10px; }
    .burger > span::after {
      top: 10px; }
  .burger--close > span {
    transition: background 0.2s 0s;
    background: transparent; }
    .burger--close > span::before, .burger--close > span::after {
      top: 0;
      transition: top 0.2s, transform 0.2s 0.2s; }
    .burger--close > span::before {
      transform: rotate3d(0, 0, 1, 45deg); }
    .burger--close > span::after {
      transform: rotate3d(0, 0, 1, -45deg); }
  .burger--no-js {
    visibility: hidden; }

